.logo {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.input-login {
    background-color: #F6F6F6;
    margin-top: 5%;
    color:#000000 ;
    border: 1px solid #000000 ; 
    border-radius: 2px;
    padding: 4%;
    width: 350px;
    font-size: 18px;   
}

.login {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 2px;
    padding: 8%;
}

.select-team {
    margin-top: 5%;
    width: 55%;
    background-color: #5DB075;
    color: white;
    border: 8px solid;
    border-radius: 25px;
    padding: 1.7%;
    font-size: 20px;
    font-weight: bolder;
}

.input-submit {
    margin-top: 5%;
    width: 45%;
    background-color: #5DB075;
    color: white;
    border: 8px solid;
    border-radius: 25px;
    padding: 1.7%;
    font-size: 20px;
    font-weight: bolder;
}

.link {
    color: black;
    font-size: 20px;
    font-weight: bolder;
    display: flex;
    justify-content: center;
}

.text {
    color: black;
    font-size: 20px;
    font-weight: bolder;
    display: flex;
    justify-content: center;
}

@media  screen and (min-width: 1366px) {
    .position-screen {
        margin-top: 10%;
        display: flex;
        flex-direction: inherit;
        align-items: center;
        justify-content: space-around;
    }
}

@media  screen and (max-width: 400px) {  
    .input-login {
        width: 50%;    
    }
    
    .input-submit {
        width: 30%;
    }

    .buda-logo {
        width: 30%;
    }

    .name-logo {
        width: 60%;
    }

}